@charset "UTF-8";
/* Functions */
/* Mixins */
article,
aside,
details,
figcaption,
figure,
footer,
header,
nav,
section,
summary,
menu,
main {
  display: block; }

audio, canvas, video, progress {
  display: inline-block;
  vertical-align: middle; }

audio:not([controls]), [hidden], template {
  display: none;
  height: 0; }

* {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
  font-size: inherit;
  line-height: inherit; }
  *:after, *:before {
    box-sizing: inherit; }

:after, :before {
  vertical-align: inherit;
  text-decoration: inherit; }

:root {
  box-sizing: border-box;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-rendering: optimizeLegibility; }

html {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent; }

a {
  background-color: transparent; }
  a:focus {
    outline: thin dotted; }
  a:active, a:hover {
    outline: 0; }

iframe, img, svg {
  vertical-align: middle; }

iframe, embed, object {
  max-width: 100%; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted; }

b, strong {
  font-weight: inherit; }

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

pre {
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: break-word;
  overflow: auto;
  margin: 1em 0; }

nav ul, nav ol {
  list-style: none; }

img {
  border: 0;
  -ms-interpolation-mode: bicubic; }

svg:not(:root) {
  overflow: hidden; }

fieldset, legend {
  border: 0; }

legend {
  white-space: normal;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0; }

select {
  cursor: pointer; }
  select:-moz-focusring {
    color: transparent;
    text-shadow: 0 0 0 #000; }

button[disabled], input[disabled] {
  cursor: default; }

button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

button, input, select, textarea {
  vertical-align: baseline;
  font: inherit;
  background: none;
  border-radius: 0;
  margin: 0; }

button,
input,
select {
  overflow: visible; }

input {
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none; }
  input::-moz-focus-inner {
    border: 0;
    padding: 0; }
  input[type=checkbox], input[type=radio] {
    cursor: pointer;
    padding: 0; }
  input[type=checkbox] {
    -moz-appearance: checkbox;
    -webkit-appearance: checkbox; }
  input[type=radio] {
    -moz-appearance: radio;
    -webkit-appearance: radio; }
  input[type=number] {
    width: auto; }
    input[type=number]::-webkit-outer-spin-button, input[type=number]::-webkit-inner-spin-button {
      height: auto; }

button,
input[type=button], input[type=reset], input[type=submit] {
  appearance: button;
  -moz-appearance: button;
  -webkit-appearance: button;
  cursor: pointer; }

textarea {
  vertical-align: top;
  overflow: auto;
  resize: vertical;
  -moz-appearance: none;
  -webkit-appearance: none; }

table {
  border-collapse: collapse;
  border-spacing: 0;
  table-layout: fixed; }

progress {
  display: inline-block;
  vertical-align: baseline; }

menu {
  margin: 1em 0;
  padding: 0 0 0 40px; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

sup {
  position: relative;
  top: -0.5em;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline; }

/******* GÉNÉRAL *******/
::-moz-selection {
  background: #000;
  color: #fff;
  text-shadow: none; }

::selection {
  background: #000;
  color: #fff;
  text-shadow: none; }

html {
  font-size: 62.5%;
  /* sets the base font to 10px for easier math */ }
  html.no-scroll {
    position: fixed;
    left: 0;
    right: 0; }

body {
  font: 1.6em / 1.2 sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

h1 {
  font-size: 5rem;
  margin: 0.67em 0; }

h2 {
  font-size: 3rem;
  margin: 0.83em 0; }

h3 {
  font-size: 2.4rem;
  margin: 1em 0; }

h4 {
  font-size: 2.2rem;
  margin: 1.33em 0; }

h5 {
  font-size: 2rem;
  margin: 1.67em 0; }

h6 {
  font-size: 1.8rem;
  margin: 2.33em 0; }

p {
  margin: 1em 0; }

blockquote {
  margin: 1em 40px; }

ol,
ul {
  padding: 0;
  margin: 1em 0; }

ul ul {
  padding: 0 0 0 25px;
  margin: 1em 0; }

ol {
  counter-reset: ol-counter; }
  ol > li {
    counter-increment: ol-counter; }
    ol > li:before {
      content: counter(ol-counter) "."; }
  ol ol {
    padding: 0 0 0 25px;
    margin: 1em 0; }

dt,
dd {
  display: inline;
  margin: 0; }

dt:after {
  content: ": "; }

dd + dt:before {
  content: "\A";
  white-space: pre; }

dd + dd:before {
  content: ", ";
  margin: 0 0 0 -0.25em; }

img {
  max-width: 100%;
  height: auto; }

code,
pre {
  display: block;
  padding: 10px 15px;
  margin: 1em 0;
  background: grey; }

hr {
  height: 1px;
  max-width: 250px;
  border: 0;
  margin: 3em auto 5em;
  background: #000; }

/******* LIENS / BOUTONS *******/
a {
  color: inherit;
  text-decoration: none;
  transition: 0.3s ease-in-out; }
  a:focus {
    outline: 0; }
  a:active {
    filter: saturate(200%); }

/******* CLASSES GENERALES  *******/
.hidden {
  display: none !important; }

.visually-hidden {
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  position: absolute;
  margin: -1px;
  overflow: hidden; }

.clearfix:after {
  content: '';
  display: table;
  clear: both; }

.pos-relative {
  position: relative; }

.align-center {
  text-align: center; }

.align-left {
  text-align: left; }

.align-right {
  text-align: right; }

.full-width {
  width: 100%; }

.icon {
  width: 1.2em;
  height: 1.2em;
  fill: currentColor; }

.img-fit.compat-object-fit {
  background-size: cover;
  background-position: 50% 50%; }
  .img-fit.compat-object-fit img {
    opacity: 0; }

.no-transition {
  transition: none !important; }

/* Placeholders */
/* Grid */
.container {
  max-width: 1200px;
  padding: 0 170px;
  margin: 0 auto; }

/* Forms */
input, textarea, select, button {
  transition: 0.3s ease-in-out; }
  input:focus, textarea:focus, select:focus, button:focus {
    outline: 0; }

input:-webkit-autofill, textarea:-webkit-autofill {
  box-shadow: 0 0 0 1000px #fff inset; }

input:invalid, textarea:invalid {
  box-shadow: none; }

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  display: none; }

select::-ms-expand {
  display: none; }

button {
  border: 0; }

/* Header */
.main-navigation {
  position: fixed;
  top: 0;
  left: 100%;
  right: -100%;
  bottom: 0;
  background: #fff;
  transition:  transform 0.23s cubic-bezier(0.25, 0.46, 0.25, 1);
  z-index: 2; }
  .main-navigation.activated {
    transform: translate3d(-100%, 0, 0); }

.main-navigation-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 96px 255px 28px; }
  .main-navigation-container .menu {
    margin: auto 0 auto;
    font-weight: 500;
    text-align: center; }
    .main-navigation-container .menu > li {
      padding: 0;
      margin: 9vh 0 0; }
      .main-navigation-container .menu > li:before {
        content: none; }
      .main-navigation-container .menu > li:first-child {
        margin-top: 0; }

.burger {
  width: 30px;
  height: 30px;
  position: relative;
  z-index: 3; }
  .burger > span {
    width: 25px;
    height: 2px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background: #000;
    transition:  background-color 0.23s 0.23s cubic-bezier(0.25, 0.46, 0.25, 1); }
    .burger > span:before, .burger > span:after {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: #000;
      border-radius: 5px;
      transform-origin: 50% 50%;
      transition:  transform 0.23s cubic-bezier(0.25, 0.46, 0.25, 1); }
    .burger > span:before {
      transform: translate(0, -9px); }
    .burger > span:after {
      transform: translate(0, 9px); }
  .burger.activated span {
    background: transparent;
    transition:  background-color 0.23s cubic-bezier(0.25, 0.46, 0.25, 1); }
    .burger.activated span:before, .burger.activated span:after {
      transition:  transform 0.23s 0.23s cubic-bezier(0.25, 0.46, 0.25, 1); }
    .burger.activated span:before {
      transform: translate(0, 0) rotateZ(-45deg); }
    .burger.activated span:after {
      transform: translate(0, 0) rotateZ(45deg); }

